.breed-select {
  width: 300px;
  color: #000;
  background-color: #fff;
  border: 1px solid tomato;
  border-radius: 5px;
  margin: 0 auto;
  padding: 10px;
  font-size: 16px;
  overflow: auto;
}

.loading {
  color: #000;
  text-align: center;
  margin-top: 10px;
  font-size: 18px;
  display: none;
  position: relative;
}

.loader {
  width: 48px;
  height: 48px;
  border-top: 3px dashed tomato;
  border-bottom: 3px solid rgba(0, 0, 0, 0);
  border-left: 3px dotted tomato;
  border-right: 3px solid tomato;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  text-align: center;
  max-width: 400px;
  background-color: #f8f8ff;
  border: 2px solid tomato;
  border-radius: 5px;
  margin: 0 auto;
  padding: 20px;
  display: none;
}

.cat-info img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
  margin-bottom: 10px;
}

.cat-info p {
  color: tomato;
  margin-bottom: 5px;
  font-size: 18px;
}

.cat-info p a {
  color: tomato;
  text-decoration: none;
}

.cat-info p a:hover {
  text-decoration: underline;
}

